import React from "react";

export default function FamilyMember({
  show,
  name,
  relation,
  spouse,
  hasKids,
  deceased,
  ...props
}) {
  return (
    <div
      {...props}
      className={`family-member ${spouse ? "spouse" : ""} ${
        deceased ? "deceased" : ""
      }`}
      style={hasKids ? { cursor: "pointer" } : {}}
    >
      <div className="name">
        {" "}
        <span className="bold">Name:</span> {name}
      </div>
      <div className="relation">
        {" "}
        <span className="bold">Relation:</span> {relation}
      </div>
      {hasKids ? <div className="more" /> : null}
    </div>
  );
}
