import React from "react";
import FamilyMember from "./FamilyMember";

export default function Generation({ generation, number, click, ...props }) {
  return (
    <div className="generation">
      {generation.map(g =>
        g.children
          ? g.children.map((child, cIndex) => (
              <div key={Math.random()} className="couple">
                {child.parents.map(parent => (
                  <FamilyMember
                    hasKids={!!child.children}
                    key={Math.random()}
                    spouse={parent.spouse}
                    deceased={parent.deceased}
                    name={parent.name}
                    relation={parent.relation}
                    onClick={() => click(child ? [child] : [])}
                  />
                ))}
              </div>
            ))
          : null
      )}
    </div>
  );
}
