import React, { useState } from "react";
import Generation from "./components/Generation";
import { theWholeDamnFamily } from "./theWholeDamnFamily";
import "./styles.css";

export default function App() {
  const [olderGens, setOlderGens] = useState([]);
  const [currentGeneration, setCurrentGeneration] = useState(
    theWholeDamnFamily
  );

  const onFamilyMemberClick = newGeneration => {
    if (
      !olderGens.find(o => {
        console.log("o", o);
        console.log("new", newGeneration);
        return o[0].children.find(p => {
          console.log("p", p);
          return p.parents[0].name === newGeneration[0].parents[0].name;
        });
      })
    ) {
      console.log("hello");
      console.log(newGeneration);
      if (newGeneration[0].children) {
        setOlderGens([...olderGens, currentGeneration]);
        setCurrentGeneration(newGeneration);
      } else {
        alert("That's as deep as she goes");
      }
    } else {
      console.log("Good bye");
      let c = [...olderGens];
      setCurrentGeneration(c.pop());
      setOlderGens([...c]);
    }
  };
  console.log("oldGen:", olderGens);
  console.log("currentGen", currentGeneration);

  return (
    <div>
      <div className="title">I Have a Large Family</div>
      <div className="generation-wrapper">
        {olderGens.map((gen, index) => (
          <Generation
            number={index}
            key={Math.random()}
            generation={gen}
            click={onFamilyMemberClick}
          />
        ))}
        <Generation
          number={999}
          generation={currentGeneration}
          click={onFamilyMemberClick}
        />
      </div>
    </div>
  );
}
